package kmm.project.demo.domain.injection

import kmm.project.demo.domain.usecase.GetTodoUseCase
import kmm.project.demo.domain.usecase.IsUserSignedInUseCase
import kmm.project.demo.domain.usecase.SignInUseCase
import kmm.project.demo.domain.usecase.SignOutUseCase
import kmm.project.demo.domain.usecase.SignUpUseCase
import org.koin.dsl.module

/**
 * Module for providing use case dependencies.
 */
internal val useCasesModule = module {
    factory { GetTodoUseCase() }

    factory { SignUpUseCase() }
    factory { SignInUseCase() }
    factory { SignOutUseCase() }
    factory { IsUserSignedInUseCase() }
}
