package io.djungle.ui.injection

import io.djungle.ui.home.HomeViewModel
import io.djungle.ui.login.LoginViewModel
import org.koin.compose.viewmodel.dsl.viewModel
import org.koin.dsl.module

/**
 * Module for providing view model dependencies.
 */
val viewModelModule = module {
    viewModel { HomeViewModel() }
    viewModel { LoginViewModel() }
}
