package kmm.project.demo.domain.repository

/**
 * A repository for authentication.
 */
interface AuthenticationRepository {

    /**
     * Signs up a user with the given email and password.
     *
     * @param email The email of the user.
     * @param password The password of the user.
     * @return `true` if the sign up was successful, `false` otherwise.
     */
    suspend fun signUp(email: String, password: String): Boolean

    /**
     * Signs in a user with the given email and password.
     *
     * @param email The email of the user.
     * @param password The password of the user.
     * @return `true` if the sign in was successful, `false` otherwise.
     */
    suspend fun signIn(email: String, password: String): Boolean

    /**
     * Signs out the current user.
     *
     * @return `true` if the sign out was successful, `false` otherwise.
     */
    suspend fun signOut()

    /**
     * Checks if a user is signed in.
     *
     * @param isAnonymousValid `true` if an anonymous user is considered signed in, `false` otherwise.
     */
    suspend fun isUserSignedIn(isAnonymousValid: Boolean = false): Boolean
}
