package kmm.project.demo.domain.usecase

import kmm.project.demo.domain.base.usecase.ActionResultUseCase
import kmm.project.demo.domain.repository.AuthenticationRepository
import org.koin.core.component.KoinComponent
import org.koin.core.component.inject

/**
 * Use case for signing up a user.
 */
class SignUpUseCase : ActionResultUseCase<SignUpUseCase.Params, Boolean>, KoinComponent {

    private val authenticationRepository: AuthenticationRepository by inject()

    override suspend fun execute(params: Params): Boolean =
        authenticationRepository.signUp(params.email, params.password)

    /**
     * The parameters for the use case.
     *
     * @property email the email.
     * @property password the password.
     */
    data class Params(
        val email: String,
        val password: String
    )
}
