package io.djungle.ui.base.compose

import androidx.compose.runtime.Composable
import androidx.compose.runtime.LaunchedEffect
import io.djungle.ui.base.viewmodel.ViewEffect
import kotlinx.coroutines.flow.Flow

/**
 * Collects and handles the view effects.
 */
@Composable
fun <VE : ViewEffect> ViewEffectHandler(viewEffectFlow: Flow<VE>, handler: (VE) -> Unit) {
    LaunchedEffect(viewEffectFlow) {
        viewEffectFlow.collect(handler)
    }
}
