package kmm.project.demo.data.dto.mapper

import kmm.project.demo.data.dto.Todo
import kmm.project.demo.domain.base.mapper.Mapper
import kmm.project.demo.domain.entity.TodoEntity

/**
 * Maps a [Todo] to a [TodoEntity].
 */
class TodoToTodoEntityMapper : Mapper<Todo, TodoEntity> {

    override fun map(input: Todo): TodoEntity = with(input) {
        TodoEntity(
            userId = userId,
            id = id,
            title = title,
            completed = completed
        )
    }
}
